import router from '@/router/routes.js'
export default {
  components: {
  },
  name: "index",
  //   mixins: [Interface],
  data() {
    return {
      routerData: [],
      defaultProps: {
        children: 'children',
        label: 'title'
      }
    }
  },
  mounted() {
    console.log(router, 'routerData');

    this.routerData = router.routes
    console.log(this.routerData, 'routerData');

  },
  methods: {
    hrefTo(data) {
      console.log(data, 'name');
      this.$router.push({ path: data.path })
    },
  },
}
</script>

